<template>
  <div class="menu">
    <!-- 轮播图 -->
    <van-swipe :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item, index) in bannerData" :key="index">
        <van-image
          class="auto-img banners"
          fit="contain"
          @click="goDetail(item.pid)"
          :src="item.bannerImg"
        >
        </van-image>
      </van-swipe-item>
    </van-swipe>


    <van-collapse v-model="activeNames" accordion>
      <div
        v-for="(item1, index1) in typeData"
        @click="toggleType(item1.isActive, item1.type, index1)"
        :key="index1"
      >
        <van-collapse-item :title="item1.typeDesc" :name="index1">
          <div class="like-box clearfix">
            <div
              v-for="(item2, index2) in currentProduct"
              :class="[index2 % 2 == 0 ? 'a' : 'b']"
              :key="index2"
              
            >
              <div @click="goDetail(item2.pid)">
                <img class="auto-img" :src="item2.smallImg" />
              </div>
              <div class="title">{{ item2.name }}</div>
              <div class="clearfix">
                <span class="fl price">￥{{ item2.price }}</span>
              </div>
            </div>
          </div>
        </van-collapse-item>
      </div>
    </van-collapse>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Menu",
  data() {
    return {
      activeNames: 0,
    };
  },
  created() {
    if (this.menuIsLoaded) {
      //如果请求过就不再请求
      return;
    }

    //加载提示
    this.$toast.loading({
      //显示遮罩层
      // mask:true,
      //禁止背景点击
      forbidClick: true,
      //展示时长为0禁止自动关闭
      duration: 0,
      //内容
      message: "正在加载...",
    });

    //初始化页面数据
    //或区域banner图数据
    this.getBanner();
    //获取商品类型
    this.getType();

    this.$store.commit("changeData", { key: "menuIsLoaded", value: true });
  },
  computed: {
    //菜单页面是否加载过
    menuIsLoaded() {
      return this.$store.state.menuIsLoaded;
    },

    ...mapState("menuModule", [
      "bannerData",
      "typeData",
      "top",
      "currentProduct",
    ]),
  },
  methods: {
    //获取bannner图
    getBanner() {
      this.axios({
        method: "GET",
        url: "/banner",
        //get请求的携带参数
        params: {
          appkey: this.appkey,
        },
      })
        .then((d) => {
          if (d.data.code == 300) {
            this.$store.commit("menuModule/changeData", {
              key: "bannerData",
              value: d.data.result,
            });
          }
        })
        .catch((err) => {});
    },

    //获取商品类型
    getType() {
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });
      this.axios({
        method: "GET",
        url: "/type",
        //get请求的携带参数
        params: {
          appkey: this.appkey,
        },
      })
        .then((d) => {
          //关闭加载提示
          this.$toast.clear();
          let types = d.data.result;
          //添加isactive，判断是否激活标签
          types.forEach((v) => {
            v.isActive = false;
          });
          types.unshift({
            type: "isHot",
            typeDesc: "热门推荐",
            isActive: "true",
          });
          if (d.data.code == 400) {
            this.$store.commit("menuModule/changeData", {
              key: "typeData",
              value: types,
            });

            //获取最新商品数据
            this.getTypeProduct(this.typeData[0].type);
          }
        })
        .catch((err) => {
          //关闭加载提示
          this.$toast.clear();
        });
    },

    //切换商品类型
    toggleType(item1, item2, index) {
      if (item1.isActive) {
        return;
      }
      let types = this.typeData.concat();
      for (let i = 0; i < types.length; i++) {
        if (types[i].isActive) {
          types[i].isActive = false;
          break;
        }
      }
      types[index].isActive = true;
      this.$store.commit("menuModule/changeData", {
        key: "typeData",
        value: types,
      });

      //加载提示
      this.$toast.loading({
        //禁止背景点击
        forbidClick: true,
        //展示时长为0禁止自动关闭
        duration: 0,
        //内容
        message: "正在加载...",
      });

      this.getTypeProduct(item2);
    },

    //获取商品类型的商品数据
    getTypeProduct(item) {
      //请求参数
      let params = {
        appkey: this.appkey,
      };
      if (item.type == "isHot" || item == "isHot") {
        params.value = 1;
        params.key = "isHot";
      } else {
        params.value = item;
        params.key = "type";
      }

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params,
      })
        .then((d) => {
          //关闭加载提示
          this.$toast.clear();

          if (d.data.code == 500) {
            this.$store.commit("menuModule/changeData", {
              key: "currentProduct",
              value: d.data.result,
            });

            //输出当前列表的商品数据
          }
        })
        .catch((err) => {
          //关闭加载提示
          this.$toast.clear();
        });
    },

    //携带商品的pid跳转详情页
    goDetail(pid) {
      this.$router.push({
        name: "Detail",
        params: { pid },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.menu {
  .banners {
    display: flex;
    height: 250px;
    border-radius: 10px;
    overflow: hidden;
  }
  
  .like-box {
    margin-top: 10px;
    > div {
      float: left;
      width: calc(~"50% - 5px - 20px");
      background-color: #fff;
      padding: 10px;
      margin-bottom: 10px;

      &.a {
        border-bottom: 1px solid #e8e8e8;
        margin-right: 5px;
      }
      &.b {
        border-bottom: 1px solid #e8e8e8;
        margin-left: 5px;
      }
      .title {
        padding: 10px 0;
        color: #444;
        font-size: 16px;
      }
      .price {
        font-size: 14px;
        color: #165dad;
        line-height: 24px;
      }
    }
  }
}
</style>
